.App {
  text-align: center;
  justify-content: center;

}

.timeline {

  display: flex;
  margin: 0 auto;
  color: whitesmoke;
  max-width: 80%;

}


.timeline li,
ul {

  color: whitesmoke;
  text-align: flex-end;
  font-size: 17px;
  font-family: 'Nunito', sans-serif;
  border: 0px;

}


.likes-counter-container {

  display: flex;
  width: 400px;
  color: #FF4C98;
  font-size: 15px;
  font-family: 'Nunito', sans-serif;
  justify-content: center;
  align-items: center;
  height: fit-content;
}


.landing-greeting-emoji-container {

  display: flex;
  align-items: flex-end;
}

.emoji {
  unicode-bidi: isolate;
  display: inline-block;
  height: fit-content;
  width: fit-content;
  font-size: 40px;
  animation: emojifloat 2s ease-in-out infinite;
  padding: 5px;

}

@keyframes emojifloat {
  0% {
    transform: translateY(0px);
  }

  50% {
    transform: translateY(-5px);
  }

  100% {
    transform: translateY(0px);
  }
}

.react-logo-spin {
  animation: App-logo-spin infinite 10s linear;

}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.noselect {

  user-select: none;
  text-decoration: none;
}

.nav-item img:hover {

  border: none;
}

ul {
  list-style: none;
  padding-left: 0;
  padding-right: 0;
}

li {
  position: relative;
  padding: 10px;
